import React, { Component } from 'react';
import { Col } from 'reactstrap';

class Widget extends Component {
    render() {
        return (
            <Col xs="6" className="mt-3">
                <h2>{this.props.title}</h2>
                <ul>
                    <li>{this.props.content}</li>
                </ul>
            </Col>
        );
    }
}

export default Widget;